<template>
  <div class="h-screen flex w-full bg-color vx-row no-gutter items-center justify-center" id="page-login">
    <div class="vx-col sm:w-1/2 md:w-1/2 lg:w-3/4 xl:w-3/5 sm:m-0 m-4">
      <vx-card class="auth-popup">
        <div slot="no-body">
          <div class="vx-row no-gutter justify-center">
            <div class="vx-col hidden lg:block lg:w-1/2">
              <figure class="logo">
                <img src="@/assets/images/logo/logo-white.svg" class="logo__image" alt="Sooonar">
                <figcaption class="logo__caption">All-in-one freight transportation<br> management system</figcaption>
              </figure>
              <img src="@/assets/images/pages/trucks.svg" alt="login" class="w-full">
            </div>
            <div class="vx-col sm:w-full md:w-full lg:w-1/2 d-theme-dark-bg">
              <div class="p-8 login-tabs-container">
                <div class="vx-card__title mb-10">
                  <h4 class="mb-4">Login</h4>
                  <p>Welcome back, please login to your account.</p>
                </div>
                <div>
                  <vs-input
                    name="email"
                    icon-no-border
                    icon="icon icon-user"
                    icon-pack="feather"
                    label-placeholder="Email"
                    v-model="username"
                    class="w-full mt-8"/>
                  <vs-input
                    type="password"
                    name="password"
                    icon-no-border
                    icon="icon icon-lock"
                    icon-pack="feather"
                    label-placeholder="Password"
                    v-model="password"
                    class="w-full mt-8" />
                  <vs-button class="float-right mt-8" @click="login">Login</vs-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </vx-card>
    </div>
    <p class="copy">COPYRIGHT &copy;2020 Hackspace. All rights Reserved</p>
  </div>
</template>

<script>
import axios from "@/axios.js"
import { mapGetters, mapActions } from 'vuex'

export default{
  data() {
    return {
      username: "Cat",
      password: "123"
    }
  },
  computed: {
    ...mapGetters("auth", ["authStatus", "isAuthenticated"])
  },
  methods: {
    login() {
      const { username, password } = this
      this.$store.dispatch("auth/authRequest", { username, password }).then(() => {
        this.$router.push('/')
      })
    }
  }
}
</script>

<style lang="scss" scoped>
  .auth-popup{
    background-color: #7ABDFF;
    border-radius: 16px;
    overflow: hidden;
  }
  .bg-color{
    background-color: #F6F8FC;
  }
  .copy{
    font-size: 16px;
    line-height: 1.4;
    color: #626262;
    position: absolute;
    left: 30px;
    bottom: 30px;
  }
  .logo{
    position: absolute;
    bottom: 50px;
    left: 50px;
    &__caption{
      padding-top: 25px;
      font-size: 14px;
      line-height: 24px;
      font-weight: 500;
      color: #ffffff;
      letter-spacing: .5px;
    }
  }
</style>
